import React from "react";
import style from "./BordsLeftStyle.module.css";

const BordsLeft = () => {
  return (
    <div className={style.leftRig}>
      <div className={style.lef}>
        <img src="./img/lef.jpg" alt="Наш логотип" />
      </div>
    </div>
  );
};
export default BordsLeft;
